<template>
  <div>
    <h3>商品列表</h3>
    <el-popover
      placement="right"
      width="500"
      trigger="click">
      <el-input
        placeholder="请输入会员名"
        clearable>
      </el-input>
      <hr />
      <el-input
        placeholder="请输入会员电话"
        clearable>
      </el-input><hr />
      <el-button type="primary" plain>确定添加</el-button>
      <el-button type="info" plain>取消添加</el-button>
      <el-button id="addBtn" type="success" slot="reference">添加</el-button>
    </el-popover>
    <el-table
      :data="goodslist"
      border
      style="width: 100%">
      <el-table-column
        prop="id"
        label="id"
        width="80">
      </el-table-column>
      <el-table-column
        prop="goodsName"
        label="商品"
        width="80">
      </el-table-column>
      <el-table-column
        prop="goodsPrice"
        label="价格"
        width="120">
      </el-table-column>
      <el-table-column
        prop="createTime"
        label="上架时间"
        width="120">
      </el-table-column>
      <el-table-column
        label="操作"
        width="200">
        <template slot-scope="props">
          <el-button  type="danger" @click="centerDialogVisible = true">删除</el-button>
          <el-dialog
            title="提示"
            :visible.sync="centerDialogVisible"
            width="30%"
            center>
            <span type="warning">确定删除吗？</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="centerDialogVisible = false">取 消</el-button>
                <el-button type="danger" plain @click="delgoods(props.row.id)">确 定</el-button>
              </span>
          </el-dialog>&nbsp;
          <el-popover
            placement="right"
            width="500"
            trigger="click">
            <el-input
              placeholder="请输入会员名"
              clearable>
            </el-input><hr />
            <el-input
              placeholder="请输入会员电话"
              clearable>
            </el-input><hr />
            <el-button type="primary" plain>确定修改</el-button>
            <el-button type="info" plain>取消修改</el-button>
            <el-button type="success" slot="reference">修改</el-button>
          </el-popover>
          <!--@click="upmember(props.row.id)"-->
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  name: 'Goods',
  data () {
    return {
      goodslist: [],
      visible: false,
      centerDialogVisible: false
    }
  },
  methods: {
    getgoodslist () {
      this.$axios.post('/api/goods/list', {
        headers: {
          'Content-Type': 'application/json'
        }
      }).then((response) => {
        console.log(response.data.data.list)
        this.goodslist = response.data.data.list
        for (var i = 0; i < this.goodslist.length; i++) {
          var newtime = new Date(this.goodslist[i].createTime)
          var crtime = newtime.getFullYear() + '-' + (newtime.getMonth() + 1) + '-' + newtime.getDate()
          this.goodslist[i].createTime = crtime
        }
      })
    },
    delgoods (id) {
      console.log(id)
    }
  },
  mounted () {
    this.getgoodslist()
  }
}
</script>

<style scope>
  #addBtn{
    float: left;
  }
</style>
