<template>
    <div>
      <h3>会员列表</h3>
      <el-popover
        placement="right"
        width="500"
        trigger="click">
            <el-input
              placeholder="请输入会员名"
              v-model="addMemderName"
              clearable>
            </el-input>
            <hr />
            <el-input
              placeholder="请输入会员电话"
              v-model="addMemderPhono"
              clearable>
            </el-input><hr />
            <el-button @click="addmember" type="primary" plain>确定添加</el-button>
            <el-button type="info" plain>取消添加</el-button>
        <el-button id="addBtn" type="success" slot="reference">添加</el-button>
      </el-popover>
      <el-table
        :data="meberlist"
        border
        style="width: 100%">
        <el-table-column
          prop=""
          label="头像 "
          width="80">
          <img id="memberhearderImg"  :src="memberHeaders" alt="">
        </el-table-column>
        <el-table-column
          prop="memberName"
          label="姓名"
          width="120">
        </el-table-column>
        <el-table-column
          prop="createTime"
          label="创建时间"
          width="150">
        </el-table-column>
        <el-table-column
          prop="memberPhone"
          label="电话"
          width="120">
        </el-table-column>
        <el-table-column
          label="操作"
          width="200"
          v-model="visible">
          <template slot-scope="props">
            <el-button  type="danger" @click="centerDialogVisible = true">删除</el-button>
            <el-dialog
              title="提示"
              :visible.sync="centerDialogVisible"
              width="30%"
              center>
              <span type="warning">确定删除吗？</span>
              <span slot="footer" class="dialog-footer">
                <el-button @click="centerDialogVisible = false">取 消</el-button>
                <el-button type="danger" plain @click="delmember(props.row.id)">确 定</el-button>
              </span>
            </el-dialog>&nbsp;
            <el-popover
              placement="right"
              width="500"
              trigger="click">
              <el-input
                placeholder="请输入会员名"
                v-model="upMemdername"
                clearable>
              </el-input><hr />
              <el-input
                placeholder="请输入会员电话"
                v-model="upMemderPhono"
                clearable>
              </el-input><hr />
              <el-button @click="upmember(props.row.id)" type="primary" plain>确定修改</el-button>
              <el-button type="info" plain>取消修改</el-button>
              <el-button type="success" slot="reference" @click="upclick(props.row.id)">修改</el-button>
            </el-popover>
            <!--@click="upmember(props.row.id)"-->
          </template>
        </el-table-column>
      </el-table>
    </div>
</template>
<script>
export default {
  name: 'Member',
  data () {
    return {
      meberlist: [],
      memberHeaders: [],
      visible: false,
      centerDialogVisible: false,
      memberIds: [],
      addMemderName: '',
      addMemderPhono: '',
      upMemdername: '',
      upMemderPhono: ''
    }
  },
  methods: {
    delclick () {
      this.centerDialogVisible = false
    },
    delmember (id) {
      console.log(id)
      this.$axios.post('/api/member/delete', { id: id }).then((response) => {
        for (var i = 0; i < this.meberlist.length; i++) {
          if (this.meberlist[i].id === id) {
            this.meberlist.splice(i, 1)
            break
          }
        }
        this.memberHeaders = []
        this.memberIds = []
        for (var j = 0; j < this.meberlist.length; j++) {
          var newtime = new Date(this.meberlist[j].createTime)
          var crtime = newtime.getFullYear() + '-' + (newtime.getMonth() + 1) + '-' + newtime.getDate()
          this.meberlist[j].createTime = crtime
          this.memberHeaders.push(this.meberlist[j].memberHeader)
          this.memberIds.push(this.meberlist[j].id)
        }
        this.centerDialogVisible = false
        this.getmemberlist()
      })
    },
    getmemberlist () {
      this.$axios.post('/api/member/list', {
        memberName: '',
        memberPhone: '',
        page: 2,
        pageSize: 5,
        status: 1,
        id: ''
      },
      {
        headers: {
          'Content-Type': 'application/json'
        }
      }).then((response) => {
        console.log(response.data.data.list)
        this.meberlist = response.data.data.list
        for (var i = 0; i < this.meberlist.length; i++) {
          var newtime = new Date(this.meberlist[i].createTime)
          var crtime = newtime.getFullYear() + '-' + (newtime.getMonth() + 1) + '-' + newtime.getDate()
          this.meberlist[i].createTime = crtime
          this.memberHeaders.push(this.meberlist[i].memberHeader)
          this.memberIds.push(this.meberlist[i].id)
        }
      })
    },
    upclick (id) {
      for (var i = 0; i < this.meberlist.length; i++) {
        if (this.meberlist[i].id === id) {
          this.upMemdername = this.meberlist[i].memberName
          this.upMemderPhono = this.meberlist[i].memberPhone
          console.log(this.upMemderName)
          break
        }
      }
      this.upMemderName = this.meberlist
    },
    upmember (id) {
      this.$axios.post('/api/member/edit', {
        memberName: this.upMemdername,
        memberPhone: this.upMemderPhono
      }).then((response) => {
        this.getmemberlist()
      })
    },
    addmember () {
      this.$axios.post('/api/member/add', {
        id: 'string',
        isDelete: 0,
        memberHeader: 'https://c-ssl.duitang.com/uploads/item/202005/09/20200509090329_PCuvG.thumb.300_300_c.jpeg',
        memberName: this.addMemderName,
        memberPhone: this.addMemderPhono,
        openId: 'string',
        status: 0
      }).then((response) => {
        this.addMemderName = ''
        this.addMemderPhono = ''
        this.getmemberlist()
      })
    }
  },
  mounted () {
    this.getmemberlist()
  }
}
</script>
<style scope>
  #memberhearderImg{
    height: 50px;
    width: 50px;
    border-radius: 25px;
  }
  #addBtn{
    float: left;
  }
</style>
